.water {
  opacity: .1;
  text-align: center;
  font-family: 'Bad Script';
  margin-top: 1rem;
  user-select: none;
}

.wrapper {
  padding: 2rem; 
  max-width: 110rem;
  margin: auto;

  .tools {
    .backbtn {
      top: 1rem;
      left: 0;
      transform: none;
    }

    .option-wrapper {
      position: absolute;
      top: 1rem;
      right: 1rem;


      .option {
        width: 5rem;
        position: absolute;
        right: 0;
        z-index: 20;
      }

      .options-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #FFF4C5;
        width: 7rem;
        padding-top: 5rem;

        .single {
          margin: 1rem;
          width: 3rem;
        }
      }
    }

    img {
      width: 5rem;
      height: 5rem;
      cursor: pointer;
      margin: 1rem;
    }
  }

  .visualize {
    max-width: 1024px;
    display: block;
    margin: auto;

    .title {
      font-size: 4rem;
      margin: 1rem;
      text-align: center;
    }

    .information {
      display: block;
      margin: auto;
      width: fit-content;
      position: absolute;
      top: 11rem;
      left: 10rem;
      font-family: 'Assistant';
      & > * {
        margin: 1rem;
        font-size: 1.5rem;
      }
    }

  }
}

.box.spinning {
  background-color: transparent;
  font-size: 3rem;
  font-weight: 700;
  font-family: 'Assistant';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20rem;
  border-radius: 4rem;
  border: #FFF4C5 1px solid;
  color: #000;
  text-align: center;
  height: fit-content;
  padding: 2rem;
  cursor:default;
}

